.esports-page {
  font-family: Arial, sans-serif;
  text-align: center;
}

.Esports-EB {
  background-color: rgb(0, 51, 102);
  min-height: 100vh;
}

.Esports-image-container {
  position: relative;
  width: 100vw;
}

.Esports-E1 {
  width: 100%;
  max-height: 90vh;
  object-fit: cover;
}
.content-wrapper {
  display: flex;
  max-width: 1500px;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.ES1-image {
  max-width: 50%;
  border-top-left-radius: 150px;
  border-bottom-right-radius: 150px;
}
.ES2{
  min-width: 40%;
  border-top-left-radius: 150px;
  border-bottom-right-radius: 150px;
  max-height: 60vh;
}
.Esports-text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(255, 215, 0);
  font-weight: bolder;
  font-size: 50px;
  border-radius: 30px;
  background-color: rgba(0, 0, 0, 0.8); 
  padding: 10px; 
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8); 
}

/* ABOUT US SECTION */
.AboutUs-overlay {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(255, 215, 0);
}
.AboutUs{
  color: rgb(255, 215, 0);
font-size: 30px;


}
/* ABOUT ME SECTION */
.AboutMe{
  color: rgb(255, 215, 0);

    font-size: 30px;
}
/* .ES2{
  max-width: 50%;
  border-top-left-radius: 150px;
  border-bottom-right-radius: 150px;
} */
.AboutMeBanner{
  min-width: 100vw;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-top:-10px;
font-weight: bolder;
font-size: 30px;
  background-color: black;
  color: rgb(255, 215, 0);
}
/* ABOUTESPORTS SECTION */


.AboutEsports{
min-width: 100vw;
padding-top: 30px;
padding-bottom: 30px;
margin-top:-10px;
font-weight: bolder;
font-size: 30px;
background-color: black;
color: rgb(255, 215, 0);
}

/* ABOUT US SECTION END */
.esports-main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

.esports-topic {
  flex: 1;
  max-width: 800px; 
  margin-top: 20px;
}

.esports-topic p {
  margin-bottom: 20px;
}
.content-wrapper {
padding-bottom: 70px;
padding-top: 50px;
}


.EsportsContactPage {
  text-align: center;
  padding-bottom: 150px;
}

.EsportsContactSection {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 500px;
  height: 500px;
}

.ContactText {
  color: rgb(255, 215, 0);
}

.EsportsContactForm {
  margin: 0 auto;
  color: rgb(255, 215, 0);
  border-radius: 30px !important;
  background-color: rgba(0, 0, 0, 0.8);
  min-height: 400px;
  min-width: 400px;
}

.EsportsContactForm input,
.EsportsContactForm textarea {
  display: block;
  width: 60%;
  height: 30px;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .EsportsContactSection {
    flex-direction: column;
  }

  .EsportsContactForm {
    min-width: 300px;
    min-height: 400px;
    border-radius: 30px;
  }
}



@media only screen and (max-width: 768px) {
  .Esports-E1 {
    height: auto;
    min-height: 60vh;
  }
  .content-wrapper {
    flex-direction: column;
    align-items: center;
   }
  

    .ES1-image {
      max-width: 100vw;


      border-radius: 0% !important;
    padding-top: 50px;
    min-height: 60vh;
    }
    .ES2 {
      min-width: 100vw;
      padding-bottom: 50px;
margin-top: -50px;
    border-radius: 0%;
    max-height: 60vh;
    }

  .Esports-text-overlay{
    font-size: 20px;
  }
  .AboutUs{
    font-size: 15px;
    font-weight: bold;
    max-width: 290px;
   
  }
  .AboutMe {
    font-size: 15px;
    font-weight: bold;
    max-width: 290px;
}
}
@media (min-width: 1500px) {
  .AboutUs {
      margin-right: 50px;
  }
  .AboutMe {
    margin-left: 50px;
}
.AboutMe-content{
  padding-bottom: 100px;

}
.AboutMeBanner{
  margin-bottom: 100px;}
.Esports-content{

  padding-bottom: 100px;

}
.AboutEsports{
  margin-bottom: 100px;

}
}